.projects-container {
  max-width: 750px !important;
  margin: auto;
  margin-top: 40px;
  text-align: center;
  padding-bottom: 40px;
}

.project-title-container h1{
  font-size: 40px;
}

.project-title-container h2 {
  margin-top: 25px;
  margin-bottom: 25px;
}

.overview-text-container {
  padding-top: 20px;
}

.overview-text-container p {
  font-size: 18px;
}

.img-wrapper {
  position: relative;
}


/* Carosel */
.left-arrow {
  position: absolute;
  top: 0;
  width: 80px;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.right-arrow {
  position: absolute;
  top: 0;
  right: 0;
  width: 80px;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.dark-arrow-container:hover .dark-arrow {
  color: white;
}

.dark-arrow {
  color: black;
}




